export default function handler(req, res) {
    const { searchParams } = new URL(req.url, `http://${req.headers.host}`);
    const orderId = searchParams.get("orderId");
  
    const orders = [
      { orderId: "12345", status: "Delivered", customerName: "John Doe", deliveryDate: "2024-12-31" },
      { orderId: "67890", status: "In-Transit", customerName: "Jane Smith", deliveryDate: "2024-12-28" },
      { orderId: "11223", status: "Awaiting Pickup", customerName: "Bob Johnson", deliveryDate: null },
    ];
  
    const order = orders.find((o) => o.orderId === orderId);
  
    if (order) {
      res.status(200).json(order);
    } else {
      res.status(404).json({ error: "Order not found" });
    }
  }
  